.App {
  text-align: center;
}

li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

ul {
  margin: 0;
  padding: 0 10px;
}

.todo-list {
  display: grid;
  grid-template-columns: 1.85fr 3fr;
  grid-template-rows: 35px 1fr;
  grid-template-areas:
    "header header"
    "u-info list";
  grid-gap: 5px;
  align-content: center;
  align-items: stretch;
  margin: 0;
  padding: 5px;
  background-color: #f5f5f5;
}

.todo-label {
  grid-area: header;
  margin: 0;
  padding: 0;
  background-color: #52565c;
  border: 1px solid #000;
  border-radius: 5px;
}

.todo-header {
  margin: 0;
  padding: 0;
  color: #ffffff;
  text-align: center;
}

.u-details {
  grid-area: u-info;
  margin: 0;
  padding: 10px 0;
  background-color: #2c4060;
  color: #ffffff;
  border: 1px solid #000;
  border-radius: 5px;
}

.u-name {
  margin: 0 0 20px;
  padding: 0;
  text-align: center;
}

.u-info {
  margin: 5px 0 0 10px;
  padding: 0;
}

.work-list {
  grid-area: list;
  margin: 0;
  padding: 10px 0;
  background-color: #d3dded;
  color: #000000;
  border: 1px solid #000;
  border-radius: 5px;
}
